.sh-burger {
  position: relative;
  display: none;
  width: 28px;
  height: 35px;
  cursor: pointer;
  transition-duration: .2s;
  margin-right: 47px; }
  .sh-burger::after {
    content: '';
    display: block;
    width: 1px;
    height: 100%;
    background: #D8D8EB;
    position: relative;
    top: 0;
    right: 0;
    margin: 0 51px; }
  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 26px;
    height: 2px;
    margin-top: 1px;
    margin-left: -13px;
    background-color: #010066; }
  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: 0.2s; }
  .sh-burger span::before {
    transform: translateY(-10px); }
  .sh-burger span::after {
    transform: translateY(6px); }
  .sh-burger--active span {
    height: 0; }
    .sh-burger--active span::before {
      transform: rotate(45deg);
      background: #323340; }
    .sh-burger--active span::after {
      transform: rotate(-45deg);
      background: #323340; }

